import JSEncrypt from 'jsencrypt'
import config from '../authing_config/config'
import pingpp from 'pingpp-js'
let util = {}
util.title = function (title) {
  title = title
    ? title + ' - Authing  |  - Authing.cn'
    : 'Authing  |  - Authing.cn'
  window.document.title = title
}

util.checkEmail = email => {
  const reg = /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/
  const isok = reg.test(email)
  return isok
}

util.checkPhone = phone => {
  const reg = /^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/
  const isok = reg.test(phone)
  return isok
}

util.clone = obj => {
  let ret, k, b
  if ((b = obj instanceof Array) || obj instanceof Object) {
    ret = b ? [] : {}
    for (k in obj) {
      if (k !== '__typename') {
        if (obj[k] instanceof Array || obj[k] instanceof Object) {
          ret[k] = util.clone(obj[k])
        } else {
          ret[k] = obj[k]
        }
      }
    }
  }
  return ret
}

util.encryption = paw => {
  const encrypt = new JSEncrypt() // 
  encrypt.setPublicKey(config.openSSLSecret) // 
  const encryptoPasswd = encrypt.encrypt(paw) // 
  return encryptoPasswd
}

